import React, { PureComponent } from "react";
import SocialActions from "../../Elements/SocialActionHover";
import CtaButton from "../../Elements/CtaButton";
import Content from "./Content";
import { RATINGS_PATH } from '../../../../constants'

export default class CardHover extends PureComponent {
  render() {    
    const {item, wall, network} = this.props;
    const isShopIcon = (item.ugc_products && item.ugc_products.length > 0 && item.hotspot === 0) ? true : false; 
    const ctaStatus = wall.ThemeRule.hideContent; 
    const nerworkColor = wall.ThemeRule.iconType == 505 ? network.color : wall.ThemeRule.iconColor;
    const isHover = wall.ThemeRule.postHover == "" || wall.ThemeRule.postHover != 1;
    const hoverCssClass = isHover ? "ts_white_bg_hover" : "ts_black_bg_hover";
    const hoverColor = { backgroundColor: isHover ? "rgb(255, 255, 255, 0.8)" : "rgba(0, 0, 0, 0.8)"};
    const styleCta ={fontFamily:wall.ThemeRule.css_font}
    const networkIcon = network.icon.replace('fa-', '');


    return (
      <div className="ts_ms_Post_hover">
        <div className="ts_card_hover_in">
          <div className={`ts_ms_card_hover_overlay`}  style={hoverColor}><div></div></div>
          <div className="ts_ms_card_hover_content">
            <div className="ts_ms_slider-social-icon">
            {(item.rating != null && item.rating != 0) ? <div className="ts_ms_social_rating" style={{backgroundImage: `url(${RATINGS_PATH}${item.networkId}/${item.rating}.png)`}} ><div></div></div>
             : <div className={`tb__icon tb-${networkIcon}`} style={{ color: nerworkColor }}><div></div></div>
            }
            </div>
            <div className="ts_ms_card_content_00">
            {isShopIcon ? ctaStatus==0 ? <Content wall={wall} item={item} isHover={isHover}/>:
            <div className={`ts_ms_card_shop_btn  ${hoverCssClass}`}>
            <CtaButton shoppingText={wall.ThemeRule.shoppingText} ctaClass={'ts_ms_shop_btn'} styleCta={styleCta}/>
            </div> :<Content wall={wall} item={item}  isHover={isHover}/>}
            </div>
            {/* <div className="ts_ms_card_shop_btn">
            {hideContent && isShopIcon ? <CtaButton shoppingText={wall.ThemeRule.shoppingText} ctaClass={'ts_ms_shop_btn'} styleCta={styleCta}/>:''}
            </div> */}

          </div>
          {wall.ThemeRule.socialAction ? <SocialActions item={item} wall={wall} network={network} />:''}
        </div>
      </div>
    );
  }
}
